.CreateGroupContainer{
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(231, 231, 231);
    position: fixed;
    top: 0%;
    left: 0;
    overflow: auto;
}

.CreateGroupBox{
    background-color: white;
    width: 25vw;
    height: 50vh;
    box-sizing: border-box;
    overflow: hidden;
    padding-top: 10px;
}
.createGroupForm>h1{
    color: #eb4034;
    font-size: 1.7vmax;
    font-family: "Roboto";
}


.createGroupForm{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    justify-content: space-evenly;
    height: 70%;
    transition: all 0.5s;
}

.createGroupForm>div{
    display: flex;
    width: 100%;
    align-items: center;
}

.createGroupForm>div>input{
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 0.9vmax "Roboto";
    outline: none;
}

.createGroupForm > div > svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
}

.createGrpBtn{
    border: none;
    background-color: tomato;
    color: white;
    font: 300 0.9vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
  }

  .createGrpBtn:hover{
    background-color: rgb(179, 66, 46);
  }

  @media screen and (max-width: 600px) {
    .CreateGroupContainer {
      background-color: white;
    }
    .CreateGroupBox {
      width: 100vw;
      height: 95vh;
    }

    .createGroupForm {
      padding: 5vmax;
    }

    .createGroupForm > div > input {
      padding: 2.5vmax 5vmax;
      font: 300 1.7vmax "Roboto";
    }
    .createGroupForm > div > svg {
      font-size: 2.8vmax;
    }
  
    .createGrpBtn {
      font: 300 1.9vmax "Roboto";
      padding: 1.8vmax;
    }
  }